import { Component } from '@angular/core';
import { MenuItem, selectMenuItemById, ThemeType } from '@simpl/element-ng';

import { SettingsService } from '../../services/settings.service';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
  host: {
    class: 'has-navbar-fixed-top si-layout-fixed-height'
  }
})
export class MainComponent {

  private languageItems: MenuItem[] = this.settingsService.availableLanguages
    .map(language => ({
      id: language,
      title: `LANGUAGE.${language.toUpperCase()}`,
      action: () => this.switchLanguage(language)
    }));
  private themeItems: MenuItem[] = [
    { id: 'auto', title: 'THEME.AUTO', action: () => this.switchTheme('auto') },
    { id: 'light', title: 'THEME.LIGHT', action: () => this.switchTheme('light') },
    { id: 'dark', title: 'THEME.DARK', action: () => this.switchTheme('dark') }
  ];

  menuItems: MenuItem[] = [
    {
      title: 'NAV.OVERVIEW',
      icon: 'element-home',
      tooltip: 'NAV.OVERVIEW',
      link: 'home'
    }
  ];

  helpItem: MenuItem = {
    title: 'Help',
    icon: 'element-help',
    items: [
      { title: 'Corporate Information', href: 'http://www.siemens.com/corporate-information', target: '_blank' }
    ]
  };

  themeItem: MenuItem = {
    title: 'NAV.THEME',
    icon: 'element-palette',
    items: this.themeItems
  };

  langItem: MenuItem = {
    title: 'NAV.LANGUAGE',
    icon: 'element-language',
    items: this.languageItems
  };


  constructor(private settingsService: SettingsService) {
    this.languageItems = selectMenuItemById(this.languageItems, this.settingsService.language);
    this.themeItems = selectMenuItemById(this.themeItems, this.settingsService.theme);
  }

  switchLanguage(language: string) {
    this.settingsService.setLanguage(language);
    this.languageItems = selectMenuItemById(this.languageItems, language);
  }

  switchTheme(theme: ThemeType) {
    this.settingsService.setTheme(theme);
    this.themeItems = selectMenuItemById(this.themeItems, theme);
  }

}
